.tour__day-3 {
  padding-top: 50px;
  padding-bottom: 50px;
  background: linear-gradient(to bottom, #fff 40%, transparent 70%, #000 95%),
    url(../img/day-3.webp) bottom center / cover no-repeat;

  &-slider {
    max-width: 760px;
    height: 358px;
    margin-top: 30px;
  }
}

@media screen and (min-width: 768px) {
  .tour__day-3 {
    padding-top: 60px;
    padding-bottom: 100px;
  }

}

@media screen and (max-width: 1439px) {
  .tour__day-3 {

    & .col-1 {
      max-width: 900px;
    }
  }
}

@media screen and (min-width: 1440px) {
  .tour__day-3 {
    margin-top: 120px;
    padding-bottom: 300px;

    & .row {
      display: flex;
      flex-direction: row-reverse;
    }

    & .col-1 {
      max-width: 510px;
      margin-left: auto;
    }
  }
}
