html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: 'Lato', sans-serif;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
  overflow: hidden;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

button:focus,
input:focus {
  outline: transparent;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
  color: #000;
}

ul {
  padding: 0;
}

li {
  list-style-type: none;
}

dl {
  margin: 0;
}

dd {
  margin-left: 0;
}

fieldset {
  border: none;
  padding-left: 0;
}

button {
  @include no-btn;
}

