.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  //margin: -1px; фикс дёргания модалки
  border: 0;
  padding: 0;

  opacity : 0;
  visibility: hidden;

  clip: rect(0 0 0 0);
  overflow: hidden;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}
