.header {
  margin: 0 auto;
  position: fixed;
  z-index: 2;
  left: 0;
  right: 0;
  top: 0;

  transition: all 0.4s ease;
}

.header .header__link {
  display: none;
}

// навешивается, как только начинается скролл
.header__scrolled {
  backdrop-filter: blur(8px);
  background-color: hsla(0, 0%, 100%, 0.5);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1),
    0px 5px 20px rgba(0, 0, 0, 0.1);
}

.header__invisible {
  transform: translateY(-9rem);
}

.header .container {
  padding: 1.2rem 15px;
}

.header .container__inner {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: auto;
}

.header__nav {
  display: none;
}

.header__menu {}

.header__menu-item {}

.header__menu-link {
  display: block;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 120%;
    width: 0%;
    height: 1px;
    background-color: #000;
    transition: 0.2s ease;
  }

  &:hover:after {
    width: 100%;
  }
}

.header__burger {
  width: 24px;
  height: 19px;

  & span {
    display: block;
    width: 100%;
    height: 3px;
    background-color: #000;
    border-radius: 10px;
  }

  & span:nth-child(2) {
    margin-top: 5px;
    margin-bottom: 5px;
  }

  margin-top: -2px;
  margin-right: 4px;
}

@media screen and (min-width: 550px) {
  .header .header__link {
    display: block;
    margin-right: 26px;
  }
}

@media screen and (min-width: 768px) {

  .header__burger {
    margin-top: 0;
  }
}

@media screen and (min-width: 900px) {

  .header .header__link {
    margin-right: 0;
    margin-left: 63px;
  }

  .header__nav {
    display: block;
  }

  .header__menu {
    display: flex;

    &>*:not(:last-child) {
      margin-right: 56px;
    }

    margin-top: -1px;
  }

  .header__burger {
    display: none;
  }
}

@media screen and (min-width: 1440px) {}
